<template>
  <div>
    <el-breadcrumb separator="/" style="margin-bottom: 30px;">
      <el-breadcrumb-item to="/">工作台</el-breadcrumb-item>
      <el-breadcrumb-item to="/member/bank">银行卡列表</el-breadcrumb-item>
      <el-breadcrumb-item>详情</el-breadcrumb-item>
    </el-breadcrumb>
    <el-form
      v-loading="loading"
      :inline="true"
      @submit.native.prevent="onSubmit"
      :model="form"
      class="form-search"
      label-width="100px"
    >
      <el-row>
        <el-col>
          <el-form-item label="银行名">
            <el-input
              size="small"
              placeholder="请输入银行名"
              v-model="form.name"
            />
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col>
          <el-form-item label="银行卡号">
            <el-input
              size="small"
              placeholder="请输入银行卡号"
              v-model="form.card"
            />
          </el-form-item>
        </el-col>
      </el-row>
      <el-row v-if="!this.$router.history.current.params.id">
        <el-col>
          <el-form-item label="会员手机号">
            <el-input
              size="small"
              placeholder="请输入手机号"
              v-model="form.mobile"
            />
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col>
          <el-button native-type="submit" type="primary">提交</el-button>
        </el-col>
      </el-row>
    </el-form>
  </div>
</template>
<script>
import { Message } from 'element-ui'
import Fetch from '../../utils/fetch'

export default {
  data() {
    return {
      loading: false,
      form: {
        card: '',
        code: 'none',
        name: '',
      },
      config: {
        bankList: [],
      },
    }
  },
  mounted() {
    this.getInfo()
  },
  methods: {
    getInfo() {
      const id = this.$router.history.current.params.id
      const url = id > 0 ? '/user/bank/view?id=' + id : '/user/bank/config'
      this.loading = true
      Fetch(url)
        .then(r => {
          this.loading = false
          if (r.data.view) {
            this.form = r.data.view
            this.config.bankList = r.data.config.code
          } else {
            this.config.bankList = r.data.code
          }
        })
        .catch(() => (this.loading = false))
    },
    onSubmit() {
      const id = this.$router.history.current.params.id
      this.loading = true
      Fetch('/user/bank/' + (id ? 'update' : 'create'), {
        id: this.$router.history.current.params.id,
        ...this.form,
      })
        .then(r => {
          this.loading = false
          Message.success(r.msg)
          this.$router.replace('/member/bank')
        })
        .catch(() => (this.loading = false))
    },
  },
}
</script>
<style lang="less" scoped>
.form-search {
  max-width: 800px;

  /deep/ .el-input {
    width: 300px;
  }
}
</style>
